/** Forza la expiraci칩n de los tokens de sesi칩n. Elimina la memoria local. Recarga la ubicaci칩n actual al finalizar.*/
export default function closeSession(): void {
    for (let cookie of document.cookie.split(";")) {
      document.cookie = cookie + "=; Secure; path=/; expires=" + new Date(0).toUTCString();
    }
    localStorage.clear();
    window.location.reload();
}